import axios from 'axios'

export default {
  //
  getProviceList() {
    return axios.get('/ptapi/getprovincecityinfo')
  },
  // 
  getRecommed(){
    return axios.get('/ptapi/recommends')
  },
    // 
  getSearch(params){
    return axios.get('/ptapi/suggest',{
      params
    })
  }
}
